      <p>ä¸ºå¤šå¼ å›¾ç‰‡æ‰¹é‡æ·»åŠ æ–‡å­—æˆ–å›¾ç‰‡æ°´å°ï¼Œæ”¯æŒä½ç½®ã€é€æ˜åº¦ã€å¤§å°è°ƒèŠ‚</p>
    </div>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
        <el-card class="config-card">
          <div slot="header">
            <span>æ°´å°è®¾ç½®</span>
          </div>
          
          <el-form label-width="80px">
            <el-form-item label="æ°´å°ç±»å‹">
              <el-radio-group v-model="watermarkType">
                <el-radio label="text">æ–‡å­—æ°´å°</el-radio>
                <el-radio label="image">å›¾ç‰‡æ°´å°</el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item v-if="watermarkType === 'text'" label="æ°´å°æ–‡å­—">
              <el-input v-model="watermarkText" placeholder="è¯·è¾“å…¥æ°´å°æ–‡å­—"></el-input>
            </el-form-item>

            <el-form-item v-if="watermarkType === 'image'" label="æ°´å°å›¾ç‰‡">
              <el-upload
                action=""
                :show-file-list="false"
                :before-upload="handleWatermarkUpload"
                accept="image/*"
              >
                <el-button size="small" type="primary">é€‰æ‹©æ°´å°å›¾ç‰‡</el-button>
              </el-upload>
              <div v-if="watermarkImage" class="watermark-preview">
                <img :src="watermarkImage" alt="æ°´å°é¢„è§ˆ" />
              </div>
            </el-form-item>

            <el-form-item label="æ°´å°ä½ç½®">
              <el-select v-model="watermarkPosition" style="width: 100%">
                <el-option label="å·¦ä¸Šè§’" value="top-left"></el-option>
                <el-option label="ä¸Šä¸­" value="top-center"></el-option>
                <el-option label="å³ä¸Šè§’" value="top-right"></el-option>
                <el-option label="å·¦ä¸­" value="middle-left"></el-option>
                <el-option label="å±…ä¸­" value="center"></el-option>
                <el-option label="å³ä¸­" value="middle-right"></el-option>
                <el-option label="å·¦ä¸‹è§’" value="bottom-left"></el-option>
                <el-option label="ä¸‹ä¸­" value="bottom-center"></el-option>
                <el-option label="å³ä¸‹è§’" value="bottom-right"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="é€æ˜åº¦">
              <el-slider v-model="watermarkOpacity" :min="0" :max="100" show-input></el-slider>
            </el-form-item>

            <el-form-item v-if="watermarkType === 'text'" label="å­—ä½“å¤§å°">
              <el-input-number v-model="fontSize" :min="12" :max="200" style="width: 100%"></el-input-number>
            </el-form-item>

            <el-form-item v-if="watermarkType === 'text'" label="å­—ä½“é¢œè‰²">
              <el-color-picker v-model="fontColor"></el-color-picker>
            </el-form-item>

            <el-form-item v-if="watermarkType === 'image'" label="æ°´å°å¤§å°">
              <el-input-number v-model="watermarkSize" :min="10" :max="500" style="width: 100%"></el-input-number>
            </el-form-item>

            <el-form-item label="è¾¹è·">
              <el-input-number v-model="watermarkMargin" :min="0" :max="200" style="width: 100%"></el-input-number>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16">
        <el-card class="upload-card">
          <div slot="header">
            <span>å›¾ç‰‡ä¸Šä¼ </span>
            <div style="float: right;">
              <el-button size="small" @click="clearAll">æ¸…ç©ºæ‰€æœ‰</el-button>
            </div>
          </div>
          
          <el-upload
            class="upload-dragger"
            drag
            action=""
            :auto-upload="false"
            :on-change="handleFileChange"
            :show-file-list="false"
            multiple
            accept="image/*"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">å°†å›¾ç‰‡æ‹–åˆ°æ­¤å¤„ï¼Œæˆ–<em>ç‚¹å‡»ä¸Šä¼ </em></div>
            <div class="el-upload__tip" slot="tip">æ”¯æŒ JPGã€PNGã€WebP æ ¼å¼ï¼Œå¯æ‰¹é‡ä¸Šä¼ </div>
          </el-upload>

          <div v-if="imageList.length > 0" class="image-list">
            <div v-for="(image, index) in imageList" :key="index" class="image-item">
              <div class="image-preview">
                <img :src="image.preview" :alt="image.name" />
                <div class="image-overlay">
                  <el-button size="mini" type="danger" @click="removeImage(index)">åˆ é™¤</el-button>
                </div>
              </div>
              <div class="image-info">
                <div class="image-name">{{ image.name }}</div>
                <div class="image-size">{{ formatFileSize(image.size) }}</div>
              </div>
            </div>
          </div>

          <div v-if="imageList.length > 0" class="action-buttons">
            <el-button type="primary" @click="processImages" :loading="processing">
              <i class="el-icon-magic-stick"></i> æ‰¹é‡æ·»åŠ æ°´å°
            </el-button>
            <el-button @click="downloadAll" :disabled="processedImages.length === 0">
              <i class="el-icon-download"></i> ä¸‹è½½å…¨éƒ¨
            </el-button>
          </div>

          <div v-if="processing" class="progress-section">
            <el-progress :percentage="progress" :status="progressStatus"></el-progress>
            <p>æ­£åœ¨å¤„ç†ç¬¬ {{ currentIndex + 1 }} / {{ imageList.length }} å¼ å›¾ç‰‡...</p>
          </div>

          <div v-if="processedImages.length > 0" class="result-section">
            <h3>å¤„ç†ç»“æœ</h3>
            <div class="result-list">
              <div v-for="(result, index) in processedImages" :key="index" class="result-item">
                <div class="result-preview">
                  <el-image
                      :src="result.dataUrl"
                      :alt="result.name"
                      :preview-src-list="processedImages.map(r => r.dataUrl)"
                  ></el-image>

                </div>
                <div class="result-info">
                  <div class="result-name">{{ result.name }}</div>
                  <el-button size="mini" @click="downloadSingle(result)">ä¸‹è½½</el-button>
                </div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'WatermarkView',
  metaInfo: {
    title: 'æ‰¹é‡å›¾ç‰‡åŠ æ°´å° - åœ¨çº¿å›¾ç‰‡æ°´å°æ·»åŠ å·¥å…·',
    meta: [
      { name: 'description', content: 'æ‰¹é‡ä¸ºå›¾ç‰‡æ·»åŠ æ–‡å­—æˆ–å›¾ç‰‡æ°´å°ï¼Œæ”¯æŒä½ç½®ã€é€æ˜åº¦ã€å¤§å°è°ƒèŠ‚ï¼Œçº¯å‰ç«¯å¤„ç†ï¼Œä¿æŠ¤éšç§ã€‚' },
      { name: 'keywords', content: 'å›¾ç‰‡æ°´å°,æ‰¹é‡åŠ æ°´å°,æ–‡å­—æ°´å°,å›¾ç‰‡æ°´å°,æ°´å°å·¥å…·,å›¾ç‰‡å¤„ç†' }
    ]
  },
  data() {
    return {
      watermarkType: 'text',
      watermarkText: 'Â© ç‰ˆæƒæ‰€æœ‰',
      watermarkImage: null,
      watermarkPosition: 'bottom-right',
      watermarkOpacity: 50,
      fontSize: 24,
      fontColor: '#ffffff',
      watermarkSize: 100,
      watermarkMargin: 20,
      
      imageList: [],
      processedImages: [],
      processing: false,
      progress: 0,
      progressStatus: '',
      currentIndex: 0
    }
  },
  methods: {
    handleFileChange(file) {
      const validTypes = ['image/jpeg', 'image/png', 'image/webp', 'image/gif']
      
      if (!validTypes.includes(file.raw.type)) {
        this.$message.error('åªæ”¯æŒ JPGã€PNGã€WebPã€GIF æ ¼å¼çš„å›¾ç‰‡')
        return false
      }

      if (file.raw.size > 10 * 1024 * 1024) {
        this.$message.error('å›¾ç‰‡å¤§å°ä¸èƒ½è¶…è¿‡ 10MB')
        return false
      }

      const reader = new FileReader()
      reader.onload = (e) => {
        this.imageList.push({
          file: file.raw,
          name: file.name,
          size: file.raw.size,
          preview: e.target.result
        })
      }
      reader.readAsDataURL(file.raw)
    },

    handleWatermarkUpload(file) {
      const validTypes = ['image/jpeg', 'image/png', 'image/webp', 'image/gif']
      
      if (!validTypes.includes(file.type)) {
        this.$message.error('æ°´å°å›¾ç‰‡åªæ”¯æŒ JPGã€PNGã€WebPã€GIF æ ¼å¼')
        return false
      }

      const reader = new FileReader()
      reader.onload = (e) => {
        this.watermarkImage = e.target.result
      }
      reader.readAsDataURL(file)
      
      return false
    },

    removeImage(index) {
      this.imageList.splice(index, 1)
    },

    clearAll() {
      this.imageList = []
      this.processedImages = []
      this.progress = 0
      this.currentIndex = 0
    },

    async processImages() {
      if (this.imageList.length === 0) {
        this.$message.warning('è¯·å…ˆä¸Šä¼ å›¾ç‰‡')
        return
      }

      if (this.watermarkType === 'text' && !this.watermarkText.trim()) {
        this.$message.warning('è¯·è¾“å…¥æ°´å°æ–‡å­—')
        return
      }

      if (this.watermarkType === 'image' && !this.watermarkImage) {
        this.$message.warning('è¯·é€‰æ‹©æ°´å°å›¾ç‰‡')
        return
      }

      this.processing = true
      this.processedImages = []
      this.progress = 0
      this.progressStatus = ''

      try {
        for (let i = 0; i < this.imageList.length; i++) {
          this.currentIndex = i
          const image = this.imageList[i]
          
          const result = await this.addWatermark(image)
          this.processedImages.push(result)
          
          this.progress = Math.round(((i + 1) / this.imageList.length) * 100)
        }
        
        this.progressStatus = 'success'
        this.$message.success(`æˆåŠŸå¤„ç† ${this.imageList.length} å¼ å›¾ç‰‡`)
      } catch (error) {
        this.progressStatus = 'exception'
        this.$message.error('å¤„ç†å›¾ç‰‡æ—¶å‡ºé”™ï¼š' + error.message)
      } finally {
        this.processing = false
      }
    },

    addWatermark(imageItem) {
      return new Promise((resolve, reject) => {
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        const img = new Image()
        
        img.onload = () => {
          canvas.width = img.width
          canvas.height = img.height
          
          // ç»˜åˆ¶åŸå›¾
          ctx.drawImage(img, 0, 0)
          
          // è®¾ç½®æ°´å°é€æ˜åº¦
          ctx.globalAlpha = this.watermarkOpacity / 100
          
          if (this.watermarkType === 'text') {
            this.addTextWatermark(ctx, canvas.width, canvas.height)
          } else {
            this.addImageWatermark(ctx, canvas.width, canvas.height)
              .then(() => {
                const dataUrl = canvas.toDataURL('image/png', 0.9)
                resolve({
                  name: imageItem.name.replace(/\.[^/.]+$/, '_watermark.png'),
                  dataUrl: dataUrl
                })
              })
              .catch(reject)
            return
          }
          
          const dataUrl = canvas.toDataURL('image/png', 0.9)
          resolve({
            name: imageItem.name.replace(/\.[^/.]+$/, '_watermark.png'),
            dataUrl: dataUrl
          })
        }
        
        img.onerror = () => reject(new Error('å›¾ç‰‡åŠ è½½å¤±è´¥'))
        img.src = imageItem.preview
      })
    },

    addTextWatermark(ctx, width, height) {
      ctx.font = `${this.fontSize}px Arial`
      ctx.fillStyle = this.fontColor
      ctx.textAlign = 'left'
      ctx.textBaseline = 'top'
      
      const textMetrics = ctx.measureText(this.watermarkText)
      const textWidth = textMetrics.width
      const textHeight = this.fontSize
      
      const position = this.calculatePosition(width, height, textWidth, textHeight)
      ctx.fillText(this.watermarkText, position.x, position.y)
    },

    addImageWatermark(ctx, width, height) {
      return new Promise((resolve, reject) => {
        const watermarkImg = new Image()
        
        watermarkImg.onload = () => {
          const aspectRatio = watermarkImg.width / watermarkImg.height
          let watermarkWidth = this.watermarkSize
          let watermarkHeight = this.watermarkSize / aspectRatio
          
          const position = this.calculatePosition(width, height, watermarkWidth, watermarkHeight)
          ctx.drawImage(watermarkImg, position.x, position.y, watermarkWidth, watermarkHeight)
          resolve()
        }
        
        watermarkImg.onerror = () => reject(new Error('æ°´å°å›¾ç‰‡åŠ è½½å¤±è´¥'))
        watermarkImg.src = this.watermarkImage
      })
    },

    calculatePosition(canvasWidth, canvasHeight, itemWidth, itemHeight) {
      let x, y
      
      switch (this.watermarkPosition) {
        case 'top-left':
          x = this.watermarkMargin
          y = this.watermarkMargin
          break
        case 'top-center':
          x = (canvasWidth - itemWidth) / 2
          y = this.watermarkMargin
          break
        case 'top-right':
          x = canvasWidth - itemWidth - this.watermarkMargin
          y = this.watermarkMargin
          break
        case 'middle-left':
          x = this.watermarkMargin
          y = (canvasHeight - itemHeight) / 2
          break
        case 'center':
          x = (canvasWidth - itemWidth) / 2
          y = (canvasHeight - itemHeight) / 2
          break
        case 'middle-right':
          x = canvasWidth - itemWidth - this.watermarkMargin
          y = (canvasHeight - itemHeight) / 2
          break
        case 'bottom-left':
          x = this.watermarkMargin
          y = canvasHeight - itemHeight - this.watermarkMargin
          break
        case 'bottom-center':
          x = (canvasWidth - itemWidth) / 2
          y = canvasHeight - itemHeight - this.watermarkMargin
          break
        case 'bottom-right':
        default:
          x = canvasWidth - itemWidth - this.watermarkMargin
          y = canvasHeight - itemHeight - this.watermarkMargin
          break
      }
      
      return { x, y }
    },

    downloadSingle(result) {
      const link = document.createElement('a')
      link.download = result.name
      link.href = result.dataUrl
      link.click()
    },

    downloadAll() {
      this.processedImages.forEach(result => {
        this.downloadSingle(result)
      })
    },

    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
  }
}
</script>

